import axios from "axios"

Vue.prototype.axios = axios;
axios.defaults.baseURL="https://manage.zhou-yuanwai.com/comprehens"

// elementUI
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);

